/* body::-webkit-scrollbar {
  display: none;
} */

*::-webkit-scrollbar {
  /* display: none; */
  width: 5px;
  background: transparent;
  margin: 0 10px;
  /* height: 5px; */
}

*::-webkit-scrollbar-thumb {
  background: black;
  border-radius: 10px;
}

/* Handle on hover */
*::-webkit-scrollbar-thumb:hover {
  background: lightgray;
  cursor: "pointer";
}
